.button {
    padding: 6px 24px;
    border-radius: 3px;
    border: none;

    font-size: 14px;
    font-weight: 600;
}

.disabled {
    opacity: 0.5;
    background-color: #004D99;

    color: #002E5C;

}

.secondary {
    border: 1px solid #06C;
    color: #0066CC;
    background-color: #fff;
}

.default {
    background-color: #06C;
    color: #FFF;
}

.red {
    background-color: #CC1439;
    color: #FFF;
}

.default:hover {
    background-color: #0080FF;
}

.red:hover {
    background-color: #FF1A47;
}

.secondary:hover {
    background-color: #E5F0FA;
}


.default:active {
    background-color: #0059B2;
}

.secondary:active {
    background-color: #C9E5FF;
}

.red:active {
    background-color: #B21232;
}

.button:disabled {
}
