.greetingForm {
}

.inputAndButtonContainer {
    display: flex;
    gap: 12px;
    margin-bottom: 10px;
}

.error {
    color: #C00;
    font-size: 14px;
    font-weight: 400;
    margin-top: 10px;
}

.input {
    font-size: 16px;
    padding: 8px 12px;
    width: 372px;
}

.input:focus {
}

.errorInput {
    border: 1px solid #CC0000;
}

.button {
    padding: 8px 24px;
    border-radius: 3px;
    border: 1px solid #06C;
    background-color: #06C;
    color: #FFF;
    font-size: 14px;
    font-weight: 600;
    line-height: 20px; /* 142.857% */
    align-self: flex-start;
}

.button:focus {
    border: 2px solid black;
}

.button:active {
    background-color: #06C;
    color: #FFF;
    opacity: 1;
}

.button:disabled {
    opacity: 0.5;
}

.text {
    color: rgba(0, 0, 0, 0.50);
    font-size: 14px;
    font-weight: 400;
}

.greeting {
    color: #000;
    font-size: 14px;
    font-weight: 500;
}

.none {
    display: none;
}