.loader {
    /*свг и/или анимация*/
    width: 50px;
    height: 50px;
    border: 5px solid #f3f3f3;
    border-top: 5px solid #3498db;
    border-radius: 50%;
    animation: spin 2s linear infinite;
    /*position: absolute;*/
    /*top: 50%;*/
    /*left: 50%;*/
    /*transform: translate(-50%, -50%);*/
}


@keyframes spin {
    0% {
        transform: translateX(50%) rotate(0deg);
    }
    100% {
        transform: translateX(50%) rotate(360deg);
    }
}