.sidebar {
    display: none;
}

.sidebar.open {
    display: block;
    min-width: 317px;
    padding: 63px 109px 0 70px;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    background: #FFFFFF;
    z-index: 5;
}

.background {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: 0.3;
    background: #000;
    z-index: 1;
}

.nav {
    display: flex;
    flex-direction: column;
    gap: 30px;
    background: #FFFFFF;

}

.nav a {
    color: #000;
    font-size: 22px;
    font-weight: 600;
    text-decoration: none;
}

.nav a:hover {
    color: #58ACFF;
}

.nav a.active {
    color: #0059B2;
    font-size: 22px;
    font-weight: 600;
    text-decoration-line: underline;
}



.close {
    position: absolute;
    top: 20px;
    right: 20px;
    background-color: transparent;
    border: none
}

.close img {
    width: 24px;
    height: 24px;
}

