.buttonsContainer {
    display: flex;
    gap: 21px;
}

.clock {
    max-width: 175px;
    margin-bottom: 8px;
}

.watch {
    color: #000;
    font-size: 20px;
    font-weight: 400;
}

.watch strong {
    color: #000;
    font-size: 20px;
    font-weight: 600;
}

.more {
    color: rgba(0, 0, 0, 0.50);
    font-size: 16px;
    font-weight: 400;
    margin-bottom: 27px;

}